.cartitems{
    margin:100px 170px ;
}
.cartitems hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;

}
.cartitems-formate-main{
    display: grid;
    grid-template-columns:0.5fr 2fr 1fr 1fr 1fr 1fr ;
    align-items: center;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
    gap: 75px;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;
}
.carticon-product-icon{
    height: 62px;
}
.cartitems-remove-icon{
  width: 15px;
  margin: 0px 40px;
  cursor: pointer;  
}
.cartitems-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: white;
}
.cartitems-down{
    display: flex;
    margin: 100px 0px;
}
.cartitems-total{
    flex:1;
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    gap: 40px;
}
.cartitems-total-item{
    display: flex;
    justify-content: space-between;
    padding:15px 0px;
}
.cartitems-total button{
    width: 262px;
    height: 58px;
    outline:none;
    border: none;
    background:#ff5a5a;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitems-peromocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitems-peromocode p{
    color: #555;
}
.cartitem-peromobox{
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #eaeaea;
}
.cartitem-peromobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.cartitem-peromobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    cursor: pointer;
}